<template>
    <div>
        <Head title="商品搜索" :show="true" ></Head>
        <van-search
            v-model="keyword"
            placeholder="请输入搜索关键词"
            show-action
            @search="onSearch"
            @cancel="onCancel"
        />
        <List 
        :goods="goods"
        @changeallgoods="getGoods"
        >

        </List>
    </div>
</template>

<script>
import {mapState,mapActions} from "vuex";

export default {
    data(){
        return {
            // keyword:"",
            // goods:[]
        }
    },
    computed:{
        ...mapState({
            goods:'searchList'
        }),
        // ...mapState(['keyword'])

        // v-model + vuex 数据修改 
        keyword:{
            get(){
                return this.$store.state.keyword;
            },
            set(newVal){
                this.$store.commit("getKeyword",newVal);
            }
        }
    },
    methods:{
        getGoods(){
            this.goods.reverse();
        },
        ...mapActions(['getSearchListAsync']),
        onSearch(){
            // enter 键盘的回车 
            console.log(this.keyword);
            this.getSearchListAsync({
                url:"/vue/getGoods",
                params:{
                     keyword:this.keyword
                }
            })
            // this.$axios.get("/vue/getGoods",{
            //     params:{
            //         keyword:this.keyword
            //     }
            // }).then(res=>{
            //     this.goods = res.data.result;
            // })
        },
        onCancel(){

        }
    }
}
</script>
